// Linear gradient mixin
@mixin linearGradient($top, $bottom) {
    background: linear-gradient(to bottom, $top 0%,$bottom 100%);
    background-color: $top;
}

@mixin ltrGradient($left, $right) {
    background: linear-gradient(to right, $left 0%,$right 100%);
    background-color: $left;
}

@mixin transition($property: all, $transition-duration: $transition-duration, $transition-delay: 0s) {
    transition: $property $transition-duration $transition-easing $transition-delay;
}

// Button color helper
@mixin button($color, $bg) {
    color: $color;
    background-color: $bg;
    border-color: $bg;

    &:hover,
    &:focus {
        color: $color;
        background-color: darken($bg, 10%);
        border-color: darken($bg, 10%);
    }
}

@mixin button-border($color, $bordercolor) {
    color: $color;
    background-color: transparent;
    border-color: $bordercolor;

    &:hover,
    &:focus {
        color: #fff;
        background-color: $bordercolor;
        border-color: $bordercolor;
    }
}

// Background hex to rgba
@mixin background-opacity($color, $opacity: 0.5) {
    background-color: rgba($color, $opacity);
}

